<template>
  <div class="hello">
    <form id="form" v-on:submit.prevent="addUser">
      <input type="text" v-model="newUser.name" placeholder="Username">
      <input type="text" v-model="newUser.description" placeholder="Description">
      <input type="submit" value="Add User">
    </form>
    <ul class="errors">
      <li v-show="!validation.name">Name cannot be empty.</li>
      <li v-show="!validation.description">Please provide a valid description.</li>
    </ul>
  </div>
</template>

<script>
  import Vue from 'vue'
  import VueSweetalert2 from 'vue-sweetalert2';
  import axios from 'axios'

  var base_url = "http://127.0.0.1:8000/apps/register";
  var base_url = "https://drfbackend.herokuapp.com/apps/register";

  export default {
    name: 'Hello',
    data () {
      return {
        newUser: {
          name: '',
          description: ''
        }
      }
    },
    computed: {
      validation: function () {
        return {
          name: !!this.newUser.name.trim(),
          description: !!this.newUser.description.trim()
        }
      },
      isValid: function () {
        debugger;
        var validation = this.validation
        return Object.keys(validation).every(function (key) {
          return validation[key]
        })
      }
    },
    // methods
    methods: {
      addUser: function () {
        if (this.isValid) {
          axios.post(base_url, JSON.stringify(this.newUser) 
                ,{
                headers: 
                {}
                // { Authorization: 'Token bc9514f0892368cfd0ea792a977aff55d53e3634', 
                // 'Content-Type': 'application/json' 
                // }
            })
            .then((response) => {
              Vue.swal(
                        'Success!',
                        'You have been registered.',
                        'success'
                    )
            })
            .catch((error) => {
              debugger;
                
            });
            this.newUser.name = ''
            this.newUser.description = ''
        }
      },
      removeUser: function (user) {
        usersRef.child(user['.key']).remove()
      }
    }    
  }
</script>

<style scoped>
body {
  font-family: Helvetica, Arial, sans-serif;
}

ul {
  padding: 0;
}

.user {
  height: 30px;
  line-height: 30px;
  padding: 10px;
  border-top: 1px solid #eee;
  overflow: hidden;
  transition: all .25s ease;
}

.user:last-child {
  border-bottom: 1px solid #eee;
}

.v-enter, .v-leave-active {
  height: 0;
  padding-top: 0;
  padding-bottom: 0;
  border-top-width: 0;
  border-bottom-width: 0;
}

.errors {
  color: #f00;
}

</style>


